import { forwardRef, PropsWithChildren, useEffect } from 'react';
import { Blockchain, BlockchainType } from '../../../constants/crosschain';
import { Logo } from './../Components/Logo';
import { Navbar } from './../Navbar';
import { AuthUser } from './AuthUser';
import { Avatar } from './Avatar';
import { Connect } from './Connect';
import { SwitchNetwork } from './SwitchNetwork';
import './topbar.scss';
// Zustand Store
import { IStore, useStore } from '../../../store';
import styled from 'styled-components';
import { Button } from '../Components/Button';
import { useOpenModal } from '../../../store/base/application/hooks';
import { ModalType } from '../../../store/base/AppSlice';

interface TopBarProps {
  mobile: boolean;
  walletConnected: boolean;
  onSelect: (chainId: any) => void;
  balance: number;
  address: string;
  explorerURL: string;
  disconnectFunc: () => void;
  transactionsHistory: any;
  walletIcon: string;
  onChainSelect: (chainId: any) => void;
  onWalletSelect: (walletType: any) => void;
  chain: Blockchain | null;
  chainType: BlockchainType;
  clearAllTransactions: () => void;
}

interface WrongNetworkButtonProps {
  mobile: boolean;
}
const WrongNetworkButton = styled(Button)<WrongNetworkButtonProps>`
  background: ${({ theme }) => theme.colors.warningError} !important;
  /* font-family: 'Poppins';
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 24px; */
  /* identical to box height */
  height: ${({ mobile }) => (mobile ? '37.5px' : '40px')};

  /* display: flex;
  align-items: center;
  text-align: center; */
  :after {
    color: ${({ theme }) => theme.colors.black} !important;
    padding: ${({ mobile }) => (mobile ? '0px' : '4px')};
  }
  margin-right: ${({ mobile }) => (mobile ? '8px' : '0px')};
  :hover {
    /* background: ${({ theme }) => theme.colors.warningError} !important; */
    box-sizing: border-box;
    outline: 1px solid map.get($text-line, 'light-grey-blue');
  }
  > div {
    display: flex;

    > svg {
      > * {
        fill: ${({ theme }) => theme.colors.black};
      }
    }
  }
`;

export const TopBar = forwardRef<TopBarProps, PropsWithChildren<any>>(
  (
    {
      mobile,
      walletConnected,
      onSelect,
      balance,
      address,
      explorerURL,
      disconnectFunc,
      transactionsHistory,
      walletIcon,
      onChainSelect,
      onWalletSelect,
      children,
      chain,
      chainType,
      clearAllTransactions,
      ...props
    },
    ref
  ) => {
    const darkMode = useStore((state: IStore) => state.themeSlice.darkMode);
    const isWrongNetwork = useStore(
      (state: IStore) => state.chainSlice.isWrongNetwork
    );
    const openWrongNetworkModal = useOpenModal(ModalType.WRONG_NETWORK);

    useEffect(() => {
      if (isWrongNetwork) {
        openWrongNetworkModal();
      }
    }, [isWrongNetwork, openWrongNetworkModal]);

    return (
      <div>
        <div className={'topbar'}>
          {mobile && (
            <Logo
              colored
              direction="horizontal"
              mode={darkMode ? 'dark' : 'light'}
              text
              style={{ width: '105.24px', height: '36px' }}
            />
          )}
          <div className="topbar-right">
            <SwitchNetwork
              mobile={mobile}
              onSelect={onSelect}
              chain={chain}
              style={{ marginRight: '10px' }}
            />
            {isWrongNetwork ? (
              <WrongNetworkButton
                className={mobile ? 'button-3' : 'button-5'}
                variant="contained"
                size="xsmall"
                style={{
                  width: '100%',
                  padding: mobile ? '8px 6px' : '4px 20px',
                }}
                onClick={() => openWrongNetworkModal()}
                text={mobile ? 'Network' : 'Wrong Network'}
                mobile={mobile}
              >
                <div>
                  <svg
                    width="22"
                    height="20"
                    viewBox="0 0 22 20"
                    fill="none"
                    xmlns="http://www.w3.org/2000/svg"
                  >
                    <path
                      d="M10.9994 16.1811L1.66919 6.21709C2.21954 5.7461 2.80515 5.33063 3.40978 4.95121L2.41214 3.95361C1.64021 4.45992 0.902682 5.0296 0.216952 5.67395C-0.0596451 5.93379 -0.0737436 6.36818 0.186046 6.64477L10.4986 17.6575C10.6282 17.7965 10.8101 17.875 11.0001 17.875H11.0008C11.1908 17.875 11.3727 17.7958 11.503 17.6568L13.7297 15.2711L12.7566 14.2981L10.9994 16.1811Z"
                      fill="white"
                    />
                    <path
                      d="M21.9995 6.11429C21.9934 5.93165 21.9149 5.75981 21.7819 5.63494C18.8379 2.88762 15.009 1.37499 11.0001 1.37499C8.90455 1.37499 6.8695 1.82559 4.97144 2.62411L2.34728 0L1.37512 0.972153L19.653 19.25L20.6251 18.2778L15.6069 13.2596L21.8155 6.60714C21.9398 6.47355 22.0062 6.29629 21.9995 6.11434L21.9995 6.11429ZM14.6338 12.2865L6.02556 3.67824C7.60676 3.08509 9.28103 2.74994 11.0001 2.74994C14.4303 2.74994 17.7174 3.96379 20.3297 6.18272L14.6338 12.2865Z"
                      fill="white"
                    />
                  </svg>
                </div>
              </WrongNetworkButton>
            ) : !walletConnected ? (
              <div>
                <Connect
                  mobile={mobile}
                  walletConnected={walletConnected}
                  onChainSelect={onChainSelect}
                  onWalletSelect={onWalletSelect}
                  chain={chain}
                />
              </div>
            ) : (
              <>
                <AuthUser
                  mobile={mobile}
                  balance={balance}
                  address={address}
                  explorerURL={explorerURL}
                  disconnectFunc={disconnectFunc}
                  transactionsHistory={transactionsHistory}
                  walletIcon={walletIcon}
                  chain={chain}
                  clearAllTransactions={clearAllTransactions}
                />
                {!mobile && <Avatar />}
              </>
            )}
            {mobile && <Navbar active mobile={mobile} {...props} />}
          </div>
        </div>
        <div
          id="main-container"
          style={{
            height: mobile ? 'calc(100vh - 151px)' : 'calc(100vh - 92px)',
            overflowY: 'auto',
            width: mobile ? '100vw' : 'calc(100vw - 260px)',
            overflowX: 'hidden',
            marginLeft: mobile ? '0px' : '260px',
          }}
        >
          {children}
        </div>
      </div>
    );
  }
);
